import {Injectable} from "@angular/core";
import {Http, Headers} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {Conteudo}    from '../models/conteudo';
import {HostService}  from '../services/host.service';


@Injectable()
export class ConteudoService {
  filesToUpload: Array<File>;
  fileIsInside: Boolean = false;
  fileIsUploaded: Boolean = false;
  fileName: string;

  constructor (private http: Http, private hostService: HostService) {
    this.filesToUpload = [];
  }

  fileNameCheck(filename:string){
    if ( /\.(jpg)$/i.test(filename) ){
      return true;
    } else {
      return false;
    }
  }

  saveConteudo(conteudo: Conteudo) {
    let url = this.hostService.host+"rest/super/conteudo/save";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(url, JSON.stringify(conteudo), {headers: headers});
  }

  upload() {
    return this.makeFileRequest(this.hostService.host+"rest/super/conteudo/upload", [], this.filesToUpload);
  }

  fileChangeEvent(files: any){
    alert("teste1");
    if(this.fileNameCheck(files[0].name)){
      this.filesToUpload = files;
      this.fileIsInside = true;
      this.fileName = files[0].name;
      this.fileIsUploaded = true;
      alert("teste2");
    } else {
      this.fileIsInside = false;
      alert("O arquivo deve estar no formato JPG.")

    }
  }

  fileChangeEvent2(fileInput: any){
    if(this.fileNameCheck(fileInput.target.files[0].name)){
      this.filesToUpload = <Array<File>> fileInput.target.files;
      this.fileIsInside = true;
      this.fileName = fileInput.target.files[0].name;
      this.fileIsUploaded = true;
      alert("Entrou cara!");
    } else {
      this.fileIsInside = false;
      alert("O arquivo deve estar no formato JPG.")

    }
  }




  fileChangeEventModal(fileInput: any){
    if(this.fileNameCheck(fileInput.target.files[0].name)){
      this.filesToUpload = <Array<File>> fileInput.target.files;
      this.fileIsInside = true;
      this.fileName = fileInput.target.files[0].name;
      this.fileIsUploaded = true;
      return fileInput.target.files[0].name;
    } else {
      this.fileIsInside = false;
      alert("O arquivo deve estar no formato PDF.")

    }
  }

  makeFileRequest(url: string, params: Array<string>, files: Array<File>) {
    return new Promise((resolve, reject) => {
      var formData: any = new FormData();
      var xhr = new XMLHttpRequest();
      for(var i = 0; i < files.length; i++) {
        formData.append("uploads[]", files[i], files[i].name);
      }
      xhr.onreadystatechange = function () {
        if (xhr.readyState == 4) {
          if (xhr.status == 200) {
            resolve();
          } else {
            reject(xhr.response);
          }
        }
      }
      xhr.open("POST", url, true);
      xhr.setRequestHeader("Authorization", "Bearer "+localStorage.getItem("token"));
      xhr.send(formData);
      this.fileIsInside = false;

    });
  }


}



